.add-post {
	background-color: #e1e1e1;
	padding: 40px 0;
}

.add-post__wrapper {
	max-width: 800px;
	margin: auto;
}

.add-post__wrapper:after {
	clear: both;
	content: "";
	display: block;
}

.add-post__input {
	width: 100%;
	display: block;
	font-size: 16px;
	line-height: 24px;
	border: none;
	-webkit-box-shadow: 0 1px 1px #999;
	        box-shadow: 0 1px 1px #999;
	border-radius: 2px;
	margin: 0 0 15px 0;
	padding: 5px 12px;
}

.add-post__textarea {
	width: 100%;
	min-height: 150px;
	display: block;
	font-size: 16px;
	line-height: 24px;
	border: none;
	-webkit-box-shadow: 0 1px 1px #999;
	        box-shadow: 0 1px 1px #999;
	border-radius: 2px;
	resize: none;
	margin: 0 0 15px 0;
	padding: 5px 12px;
}

.add-post__button {
	display: inline-block;
	float: right;
	color: #fff;
	padding: 10px 60px;
	background-color: #333;
	border-radius: 2px;
	-webkit-box-shadow: 0 1px 1px #333;
	        box-shadow: 0 1px 1px #333;
	border: none;
	font-size: 16px;
	cursor: pointer;
	-webkit-transition: all 150ms;
	-o-transition: all 150ms;
	transition: all 150ms;
}

.add-post__button:hover {
	background-color: #555;
}

.add-post__button:active {
	background-color: #000;
}



